<template>
	<view class="all">
		<view class="head">
			添加纪念日
		</view>
		<form @submit="formSubmit">
		<input name="input" class="title" maxlength="16" confirm-type="done" type="text" placeholder="请输入标题" auto-blur='true' />
		<textarea class="content" name="textarea" confirm-type="done" type="text" placeholder="今天发生了什么？纪念日详情在这里请填写吧~"
			auto-blur='true'></textarea>
		<view class="images">
			<view class="imageItem">
				<image v-if="images.length>0" v-for="(item,index) in images" :key="index" :src="item" mode=""
					@click="remove(index)"></image>
			</view>
			<view class="selectImages" @click="chooseImages">
				<image src="../../static/icon/照相机.png" mode=""></image>
			</view>
		</view>
		<view class="day">
			<view>纪念日期</view>
			<picker name="picker" mode="date" @change="bindDateChange">
				<view>{{date}}</view>
			</picker>
		</view>
			<button form-type="submit">发布</button>
		</form>
	</view>
</template>

<script>
	export default {
		data() {
			const currentDate = this.getDate({
				format: true
			})
			return {
				images: [],
				date: currentDate
			};
		},
		methods: {
			formSubmit(e){
				console.log(e.detail.value)
				uni.switchTab({
					url:'../Home/Home'
				})
			},
			chooseImages() {
				uni.chooseImage({
					success: (res) => {
						res.tempFilePaths.forEach((item) => {
							this.images.push(item)
						})
					}
				})
			},
			remove(index) {
				this.images.splice(index, 1)
			},
			bindDateChange(e){
				this.date=e.detail.value
			},
			getDate(type) {
				const date = new Date();
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();

				if (type === 'start') {
					year = year - 60;
				} else if (type === 'end') {
					year = year + 2;
				}
				month = month > 9 ? month : '0' + month;
				day = day > 9 ? day : '0' + day;
				return `${year}-${month}-${day}`;
			}
		}
	}
</script>

<style lang="scss">
	.all {
		margin: auto 20rpx;

		.title {
			width: 750rpx;
			margin: 20rpx auto;
			padding-bottom: 20rpx;
			font-size: 30rpx;
			border-bottom: 1px solid #eee;
		}

		.content {
			width: 750rpx;
			margin: 20rpx auto;
			font-size: 30rpx;
		}

		.images {
			display: flex;
			width: 750rpx;
			padding-bottom: 20rpx;

			.selectImages {
				width: 150rpx;
				height: 150rpx;
				background-color: #a6a6a6;
				display: flex;
				justify-content: center;
				align-items: center;

				image {
					width: 100rpx;
					height: 100rpx;
				}
			}

			.imageItem {
				image {
					position: relative;
					margin-right: 20rpx;
					width: 150rpx;
					height: 150rpx;
					pointer-events: none;

					&::before {
						color: white;
						content: '-';
						font-size: 45rpx;
						position: absolute;
						display: block;
						top: -20rpx;
						left: 120rpx;
						width: 50rpx;
						height: 50rpx;
						border-radius: 50%;
						background: black;
						pointer-events: auto;
					}
				}
			}
		}

		.day {
			border-top: 1px solid #eee;
			border-bottom: 1px solid #eee;
			display: flex;
			height: 80rpx;
			align-items: center;
			justify-content: space-between;
		}

	}
</style>